import Vue from 'vue'
import Router from 'vue-router'
import home from '@/views/home'
import members from '@/views/members'
import projects from '@/views/projects'
import publications from '@/views/publications'
import news from '@/views/news'
import videos from '@/views/videos'
import FQAs from '@/views/FQAs'
import post from '@/components/post'
import projectList from '@/components/projectList'
import cwyou from '@/views/member/cwyou'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/members/all_members',
      name: 'members',
      component: members
    },
    {
      path: '/members/cwyou',
      name: 'cwyou',
      component: cwyou
    },
    {
      path: '/projects',
      name: 'projects',
      component: projects,
      children: [
        {
          path: 'post/:id',
          name: 'project',
          component: post,
        },
        {
          path: 'all_projects',
          name: 'all_projects',
          component: projectList,
        }
      ]
    },
    {
      path: '/publications/publications',
      name: 'publications',
      component: publications
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/post/:id',
      name: 'post',
      component: post,
    },
    {
      path: '/videos',
      name: 'videos',
      component: videos
    },
    {
      path: '/FQAs',
      name: 'FQAs',
      component: FQAs
    }
  ]
})



