<template lang="pug">
#PageContainer
    <router-view :projects="projects"/>
</template>

<script>
export default {
    components: {
    },
    data () {
        return {
            projects: [
                {
                    category: "Ongoing Research Project",
                    projects: [
                        {
                            title: "Clinical-friendly Drug Psychotherapy with Virtual Reality and Biofeedback Technologies",
                            image: require('@/assets/project1.jpg'),
                            id: "vr-drug",
                            content: "Virtual Reality has demonstrated its potential in drug psychotherapy. However, there is little research on the design space of VR experience combined with biofeedback in clinical settings for drug addiction that is aligned with treatment goals. This project explores how to incorporate virtual reality and biofeedback technologies to assist psychotherapists in raising patients' awareness and identifying their cues in psychotherapy. We are currently collaborating with treatment experts from Taipei City Hospital to apply these technologies in real clinical practices.",
                            body: "Chieh-Jui Ho, Chi-Ting Hou, Min-Wei Hung, Chien Wen (Tina) Yuan, Nanyi Bi, Ming-Chyi Huang, Chuang-Wen You. CravingProbe: A System Combining Virtual Reality and Biofeedback Technologies to Assist Drug Psychotherapy, accepted in The First Workshop on Multiple Input Modalities and Sensations for VR/AR Interactions (MIMSVAI 2021). ",
                            pdf: "",
                            video: "https://youtu.be/mtVRF1HeZgI"
                        }
                    ]
                }
            ]
        }
    }
}
</script>


<style lang="sass" scoped>
@import "@/style/value.sass"
#PageContainer
    max-width: 1280px
    margin: auto
</style>



