<template lang="pug">
#ProjectList
    .projectContainer(v-for="cate in projects")
        .blockTitle {{cate.category}}
        .projectBlock(v-for="project in cate.projects")
            .projectImg(:style="{'background-image': 'url('+project.image+')'}")
            router-link.projectTitle(:to="'post/'+project.id") {{project.title}}
            .projectContent {{project.content}}
    
</template>

<script>
export default {
    props: ['projects'],
    components: {
    },
    data () {
        return {
        }
    }
}
</script>


<style lang="sass" scoped>
@import "@/style/value.sass"
.projectImg
    width: 300px
    height: 240px
    background-size: cover
    background-position: center
.projectTitle
    font-weight: bold
    font-size: 1.2em
    cursor: pointer
</style>



