<template lang="pug">
#postApp
    h1.articlePageTitle {{post_data.title}}
    .articlePageJumpbar(:style="{'background-image': 'url('+post_data.image+')'}")
    div {{post_data.content}}
    .HTMLContainer(v-html="post_data.body")
    a.projectPdf(v-if="post_data.pdf!=''" :href="post_data.pdf" target="blank") [PDF]
    a.projectVideo(v-if="post_data.video!=''" :href="post_data.video" target="blank") [video]

</template>

<script>

export default {
    props: ['projects'],
    data() {
        return {
            post_data: {}
        }
    },
    created() {
        let post_id = this.$route.params.id;
        for(let i=0;i<this.projects.length;i++){
            for(let j=0;j<this.projects[i].projects.length;j++){
                if(post_id == this.projects[i].projects[j].id){
                    this.post_data = this.projects[i].projects[j];
                }
            }
        }
        
    },
    methods: {
       
    }
}

</script>

<style lang="sass" scoped>

@media all and (max-width: 760px)
    img
        width: 100%

.articlePageJumpbar
    background-size: cover
    max-width: 1080px
    height: 480px
    margin: auto
    background-position: center
#hashTag
    color: #777
    margin-right: 20px
.author,.dateNote
    margin: 10px
</style>


