<template lang="pug">
#PageContainer
    .projectContainer(v-for="cate in publications")
        .blockTitle {{cate.category}}
        .projectBlock(v-for="project in cate.projects")
            .projectImg(:style="{'background-image': 'url('+project.image+')'}")
            .projectTitle {{project.title}}
            .projectAuthor {{project.author}}
            .projectConerence {{project.conference}}
            a.projectPdf(v-if="project.pdf!=''" :href="project.pdf" target="blank") [PDF]
            a.projectVideo(v-if="project.video!=''" :href="project.video" target="blank") [video]
</template>

<script>
export default {
    components: {
    },
    data () {
        return {
            publications: [
                {
                    category: "2021",
                    projects: [
                        {
                            title: "TechLifeProbe: A Technology Prototype to Probe How Data Sharing With Adolescents' Parents Improves Their Technology Abuse Through Mobile Phones",
                            author: "Pin-Chieh Chen, Min-Wei Hung, Hsueh-Sung Lu, Chien Wen (Tina) Yuan, Nanyi Bi, Wan-Chen  Lee, Ming-Chyi Huang, Chuang-Wen You.",
                            image: require('@/assets/project1.jpg'),
                            conference: "",
                            pdf: "https://dl.acm.org/doi/abs/10.1145/3411764.3445278%22",
                            video: ""
                        }
                    ]
                }
            ]
        }
    }
}
</script>


<style lang="sass" scoped>
@import "@/style/value.sass"
#PageContainer
    max-width: 1280px
    margin: auto
</style>



