<template lang="pug">
#PageContainer
    .videoBlock(v-for="video in videos")
        youtube.video(:video-id="video.src" player-width="360" player-height="240")
        .videoTitle {{video.title}}
</template>

<script>
export default {
    components: {
    },
    data () {
        return {
            videos: [
                {
                    title: "",
                    src: "mtVRF1HeZgI"
                },
                {
                    title: "",
                    src: "vGVaI6lkbeU"
                },
                {
                    title: "",
                    src: "7gU0Zqo1uDs"
                }
            ]
        }
    }
}
</script>


<style lang="sass" scoped>
@import "@/style/value.sass"
#PageContainer
    display: flex
    flex-wrap: wrap
    justify-content: center
.video
    margin: 20px
</style>



